import React from 'react';
import { useNavigate } from 'react-router-dom';
import styled from 'styled-components';

const HeroSection = styled.div`
  height: calc(100vh - 70px);
  background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)),
    url('/hero-image.jpg') center/cover;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: white;
`;

const HeroContent = styled.div`
  max-width: 800px;
  padding: 0 24px;
`;

const Title = styled.h1`
  font-size: 48px;
  margin-bottom: 24px;
`;

const Subtitle = styled.p`
  font-size: 20px;
  margin-bottom: 32px;
  color: #ccc;
`;

const CityTabs = styled.div`
  display: flex;
  gap: 12px;
  justify-content: center;
  flex-wrap: wrap;
  margin-bottom: 32px;
`;

const CityTab = styled.button`
  padding: 12px 24px;
  background: ${props => props.active ? '#4a9eff' : 'rgba(255, 255, 255, 0.1)'};
  color: white;
  border: none;
  border-radius: 24px;
  cursor: pointer;
  transition: all 0.2s;

  &:hover {
    background: #4a9eff;
  }
`;

const CTAButton = styled.button`
  padding: 16px 32px;
  background: #4a9eff;
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 18px;
  cursor: pointer;
  transition: all 0.2s;

  &:hover {
    background: #3a8eff;
    transform: translateY(-2px);
  }
`;

const LandingPage = () => {
  const navigate = useNavigate();
  const topCities = ['Mumbai', 'Delhi', 'Bangalore', 'Hyderabad', 'Chennai'];

  const handleCityClick = (city) => {
    navigate(`/courts?city=${city}`);
  };

  return (
    <>
      <HeroSection>
        <HeroContent>
          <Title>Discover Pickleball Courts Near You</Title>
          <Subtitle>
            Find and explore pickleball courts across India. Join the fastest-growing
            sport community today!
          </Subtitle>
          <CityTabs>
            {topCities.map(city => (
              <CityTab
                key={city}
                onClick={() => handleCityClick(city)}
              >
                {city}
              </CityTab>
            ))}
            <CityTab onClick={() => navigate('/courts')}>
              All Cities
            </CityTab>
          </CityTabs>
          <CTAButton onClick={() => navigate('/courts')}>
            Find Courts Now
          </CTAButton>
        </HeroContent>
      </HeroSection>
    </>
  );
};

export default LandingPage; 